"use client";

import React, { useRef, useEffect, useState, useCallback } from 'react';
import mapboxgl from 'mapbox-gl';
import 'mapbox-gl/dist/mapbox-gl.css';

// Placeholder token - User needs to replace this!
const MAPBOX_TOKEN = process.env.NEXT_PUBLIC_MAPBOX_TOKEN || "pk.eyJ1IjoiZXhhbXBsZSIsImEiOiJjbTV..."

mapboxgl.accessToken = MAPBOX_TOKEN;

export default function MapComponent() {
    const mapContainer = useRef<HTMLDivElement>(null);
    const map = useRef<mapboxgl.Map | null>(null);
    const [lng, setLng] = useState(-122.4194);
    const [lat, setLat] = useState(37.7749);
    const [zoom, setZoom] = useState(14);
    const [address, setAddress] = useState<string>("Hover map to see address");
    const [loading, setLoading] = useState(false);

    // Debounce the API call
    const fetchAddress = useCallback(async (lat: number, lon: number) => {
        try {
            setLoading(true);
            const res = await fetch(`http://localhost:8000/encode?lat=${lat}&lon=${lon}&lang=en`);
            if (res.ok) {
                const data = await res.json();
                setAddress(data.words);
            } else {
                setAddress("Error fetching address");
            }
        } catch (e) {
            setAddress("Error connecting to API");
        } finally {
            setLoading(false);
        }
    }, []);

    useEffect(() => {
        if (map.current) return; // initialize map only once
        if (!mapContainer.current) return;

        map.current = new mapboxgl.Map({
            container: mapContainer.current,
            style: 'mapbox://styles/mapbox/streets-v12',
            center: [lng, lat],
            zoom: zoom
        });

        map.current.on('move', () => {
            if (!map.current) return;
            setLng(parseFloat(map.current.getCenter().lng.toFixed(4)));
            setLat(parseFloat(map.current.getCenter().lat.toFixed(4)));
            setZoom(parseFloat(map.current.getZoom().toFixed(2)));
        });

        map.current.on('mousemove', (e) => {
            // Debounce logic could be here, but for simplicity we call directly or use a timeout
            // Let's use a simple timeout approach or just call it (API is local, it's fast)
            // For better performance, we should debounce.
            const { lng, lat } = e.lngLat;
            fetchAddress(lat, lng);
        });
    }, [lat, lng, zoom, fetchAddress]);

    return (
        <div className="w-full h-screen relative">
            <div ref={mapContainer} className="map-container w-full h-full" />

            <div className="absolute bottom-10 left-1/2 transform -translate-x-1/2 bg-white/90 backdrop-blur-md p-6 rounded-2xl shadow-2xl border border-gray-200 text-center min-w-[300px]">
                <h2 className="text-sm uppercase tracking-widest text-gray-500 mb-1">Locates.me Address</h2>
                <div className="text-2xl font-bold text-gray-900 font-mono">
                    {loading ? <span className="opacity-50">...</span> : address}
                </div>
                <div className="text-xs text-gray-400 mt-2">
                    {lat.toFixed(6)}, {lng.toFixed(6)}
                </div>
            </div>

            {!process.env.NEXT_PUBLIC_MAPBOX_TOKEN && (
                <div className="absolute top-0 left-0 w-full bg-red-500 text-white text-center p-2 text-sm font-bold">
                    WARNING: Missing NEXT_PUBLIC_MAPBOX_TOKEN in .env.local
                </div>
            )}
        </div>
    );
}
